<template>
    <div class="DetailBox">
        <h1 class="detTitle">{{det.k_title}}</h1>
        <div class="infod">
            写作人<i>{{det.k_user_name}}</i>{{det.k_date | toTime}}
        </div>
        <div class="DetTontent" v-html="det.k_content"></div>
    </div>
</template>
<script>
import url from "../../common/api";
export default {
    data(){
        return{
            det:{},
            solution:{},
            ISsolution:false
        };
    },
    methods:{
        Fneditknowledge(){//查看单个的知识库详情
        if(this.$route.query.k_type=='行业资讯'){
          url.editknowledge({
              k_id:this.$route.query.k_id
          })
          .then(res=>{
              console.log(res,'---------查看详情')
              if(res.data.code[0]='200'){
                  let xx=res.data.data[0].fields
                  xx.k_id=res.data.data[0].pk
                  this.det=xx
              }
          })
        }else{
          url.viewenterprise({
              k_id:this.$route.query.k_id
          })
          .then(res=>{
              console.log(res,'---------11111111111111查看详情')
              if(res.data.code[0]='200'){
                  let xx=res.data.data[0].fields
                  xx.k_id=res.data.data[0].pk
                  this.det.k_id=xx.k_id
                  this.det.k_title=xx.e_title
                  this.det.k_user_name=xx.e_user_name
                  this.det.k_date=xx.e_date
                //   this.det.k_content=xx.e_content
                  this.$set(this.det,'k_content',xx.e_content)
                console.log(this.det,'------------------')
              }
          })
        }

      },
    },
    mounted(){
        this.Fneditknowledge()//知识库的展示内容
    }
}
</script>
<style lang='stylus' scoped>
    .SDtit
      font-size 30px
      margin 30px 0
    .DetailBox
        width 700px
        margin 0 auto
        .detTitle
            font-size: 22px;
            line-height: 1.4;
            margin-bottom: 14px;
        .infod
            font-size 14px
            color #999
            i 
                margin 0 10px
                color #576b95
        .DetTontent
            color #595757
            font-size 15px
            letter-spacing 1px
            line-height 1.6
            margin-top 25px
            img 
                max-width 700px
                display block
                margin 0 auto
            .logo
                width 90px
                height 90px
            p 
                text-indent 2em

</style>


